<template>
<div>
    <div class="attence-panel" :style="{ height: wrapperHeight + 'px' }">
        <div class="attence">
            <div class="attence-geog">
                <div class="attence-marker">
                    <p class="attence-day">{{todayDate}}</p>
                    <div class="attence-place">
                        <i class="jcm-place"></i>
                    </div>
                    <div class="attence-loading" v-if="loading">
                        ...
                    </div>
                    <div class="attence-location" v-show="!loading">
                        <p class="attence-title">{{current && current.name}}
                            <router-link to="map">
                                <i class="jcm-youjiantou1"></i>
                            </router-link>
                        </p>
                        <p class="attence-lieu">{{current && current.address}}</p>
                    </div>
                </div>
            </div>
            <div class="attence-card" :class="{'active': !isCard}">
                <div @click="signIn">
                    <span class="time">{{todayTime}}</span>
                    <span class="slot">{{afternoon}}</span>
                </div>
            </div>
        </div>
        <note :today="todayDate"></note>
        <div style="height: 20px;"></div>
    </div>
    <mt-actionsheet :actions="actions" v-model="sheetVisible" :cancelText="''"></mt-actionsheet>
</div>
</template>
 
<script>
    import { Toast, MessageBox, Actionsheet } from 'mint-ui';
    import { mapGetters } from 'vuex';
    import Vue from 'vue';
    import VueAMap from 'vue-amap';
    import Note from './note.vue';
    import { lazyAMapApiLoaderInstance } from 'vue-amap';

    Vue.use(VueAMap);
    VueAMap.initAMapApiLoader({
        key: '57cf654c08f036263991d0f13fd1fa38',
        plugin: ['AMap.Geolocation', 'AMap.PlaceSearch']
    });

    let $textarea = null;
    export default {
        components: {
            Note
        },
        activated() {
            this.getData();
        },
        data() {
            this.now = new Date();
            return {
                // 
                wrapperHeight: 500,
                //
                todayDate: this.now.getFullYear() + '' + (this.now.getMonth() + 1) + '' + this.now.getDate() + '',
                //loading
                loading: true,
                //
                afternoon: '',
                //
                todayTime: '',
                //
                isCard: true,
                //
                sheetVisible: false,
                //
                actions: []
            }
        },
        mounted() {
            this.wrapperHeight = document.documentElement.clientHeight - this.$el.getBoundingClientRect().top;
            this.getLocation();
        },
        methods: {
            getData() {
                let _this = this;   
                _this.Api.ATTENCE.initAttence().then((res) => {
                    if (res.data.code === '000000') {
                        _this.parseTime(res.data.body);
                    } else {
                        Toast(res.data.errormsg);
                        _this.isCard = false;
                    }
                }).catch((e) => {
                    _this.isCard = false;
                    Toast('!!!');
                });
            },
            //, 
            parseTime(obj) {
                let _this = this;
                //
                _this.nowObj = obj;
                //vuex  
                _this.$store.commit('updateToday', _this.nowObj);
                //
                let now = new Date(obj.time);
                //
                _this.todayTime = obj.hour + ':' + obj.minute;
                /**
                 * -1 
                 * 0 
                 * 1 
                 * 2 
                 * 3 
                 * 4 
                 */
                //, ,
                //_this.isCard = _this.isCard ? parseInt(obj.signFlag) >= 0 : false;
                _this.isCard = _this.status(obj.signFlag);
                //
                _this.afternoon = _this.getSignInMessage();
                //
                _this.todayDate = now.getFullYear() + '' + (now.getMonth() + 1) + '' + now.getDate() + '';
                //
                _this.actionsheet();
            },
            /**
             * @method 
             * -1  
             * 0   
             * 1   
             * 2   
             * 3   
             * 4   
             */
            status(ste){
                let card = parseInt(ste);
                //4
                //0
                return card == 4 ? false : card >= 0;
            },
            getSignInMessage(){
                return this.nowObj.signFlag == 4 ? '' : this.nowObj.signFlag == 1 ? '' : '';
            },
            //
            actionsheet() {
                const ATTENCEMESSAGES = {
                    '0': '',
                    '1': '',
                    '2': ',',
                    '3': ',',
                    '4': ''
                }
                this.actions = [{
                    name: ATTENCEMESSAGES[this.nowObj.signFlag],
                }, {
                    name: '',
                    method: this.attenceIn
                }, {
                    name: ''
                }];
            },
            //
            getLocation() {
                let _this = this;
                lazyAMapApiLoaderInstance.load().then(() => {
                    _this.map = new AMap.Map('map-container', {
                        resizeEnable: true,
                        zoom: 11,
                        center: [125.250554, 43.79853]
                    });
                    _this.map.plugin('AMap.Geolocation', function() {
                        let geolocation = new AMap.Geolocation({
                            enableHighAccuracy: true, //:true
                            timeout: 10000, //10
                            maximumAge: 0, //00
                            convert: true, //true
                            showButton: true, //true
                            buttonPosition: 'LB', //'LB'
                            buttonOffset: new AMap.Pixel(10, 20), //Pixel(10, 20)
                            showMarker: true, //true
                            showCircle: true, //true
                            panToLocation: true, //true
                            zoomToAccuracy: true //false
                        });
                        _this.map.addControl(geolocation);
                        geolocation.getCurrentPosition();
                        AMap.event.addListener(geolocation, 'complete', _this.onComplete); //
                        AMap.event.addListener(geolocation, 'error', _this.onError); //
                    });
                });
            },
            //
            onComplete(data) {
                //
                this.coordinates = {};
                this.coordinates.lng = data.position.getLng();
                this.coordinates.lat = data.position.getLat();
                this.service();
            },
            //
            onError() {
                //,
                this.isCard = false;
                Toast(',...');
            },
            //
            service() {
                let _this = this;
                AMap.service('AMap.PlaceSearch', () => { //
                    //PlaceSearch
                    _this.placeSearch = new AMap.PlaceSearch({
                        type: '',
                        pageSize: 16
                    });
                    _this.searchKeyWord()
                })
            },
            //, ,,
            searchKeyWord() {
                let _this = this;
                _this.placeSearch.searchNearBy('', [_this.coordinates.lng, _this.coordinates.lat], 300, (status, result) => {
                    if (status === 'complete' && result.info === 'OK') {
                        if (result.poiList.pois.length) {
                            _this.$store.commit('updatePositions', result.poiList.pois)
                        }else{
                            this.isCard = false;
                        }
                    }else{
                        this.isCard = false;
                    }
                    _this.loading = false;
                });
            },
            //
            signIn() {
                if (this.isCard) {
                    if (parseInt(this.nowObj.signFlag) > 1) {
                        this.sheetVisible = true;
                    } else {
                        this.attenceIn();
                    }
                }
            },
            attenceIn() {
                let _this = this;
                _this.Api.ATTENCE.attenceIn({
                    lat: _this.current.location.lat, //
                    lng: _this.current.location.lng, //
                    position: _this.current.address, //
                    companyName: _this.current.name, //
                    message: '' //
                }).then((res) => {
                    if (res.data.code === '000000') {
                        _this.$router.push({
                            name: 'Success',
                            params: {
                                time: res.data.body,
                                name: _this.current.name
                            }
                        });
                    } else {
                        Toast('...');
                    }
                    //
                }).catch(() => {})
            }
        },
        computed: {
            ...mapGetters(['current'])
        }
    }
</script>
 
<style>
</style>


